import { useState } from "react";
import { flushSync } from "react-dom";
import QuizPage from "./Component/english_quiz/Component/QuizPage";
function App() {
  const [state, setState] = useState({});

  const changeQuestion = (data,isShowQuestion) => {
    flushSync(() => {
      setState({});
    });
    setState({ question_data:data,isShowQuestion:isShowQuestion||false });
  };
  window.changeQuestion = changeQuestion;
   
  return <>{Object.keys(state?.question_data||{}).length ? <QuizPage obj={state?.question_data} isShowQuestion={state?.isShowQuestion}/> : ""}</>;
}

export default App;
